<template>
  <div class="page-wrp">
    <div class="fill-bg"></div>
    <h1 class="page-title">{{departWatchDoctor.DoctorName}}的患者 (共{{sortPatients.length}}名患者)</h1>
    <div class="clearfix filters">
      <div class="filter-wrp">
        <p @click="handleShowFilters">
          {{currentFilter.text}}
          <span><icon :name="showFilter ? 'angle-up' : 'angle-down'"></icon></span>
        </p>
        <ul v-show="showFilter">
          <li v-for="(filter, index) in filters"
            :key="index" @click="changeFilter(filter)"
            :style="{color: currentFilter.rule === filter.rule ? '#000' : '#ccc'}">
            {{filter.text}}
          </li>
        </ul>
      </div>
    </div>
    <div class="patients-wrp">
      <ul class="patients">
        <li class="patient clearfix" v-for="patient in sortPatients"
          :key="patient.PatientId">
          <div class="clearfix patient-right" @click="goToDetail(patient)">
            <div class="patient-info">
              <img :src="patient.photo" class="patient-photo" alt="">
              <p>{{patient.Name}}</p>
              <span>{{patient.sex}}</span>
              <span class="patient-age">{{patient.age}}岁</span>
            </div>
            <div class="patient-test">
              <p>
                <span class="enroll-time">{{patient.enrollDays}}</span>
              </p>
              <p class="count-text">
                {{patient.testCount ? ('近7天测量' + patient.testCount + '次 低血糖' + patient.lowCount + '次')
                  : '近7天无测量'
                }}
              </p>
              <p class="tags">
                <span v-for="(tag,index) in patient.tags" :key="index" class="tag"
                :style="{color: tag === '高血糖' ? '' : '#D73A2E'}">
                  <img
                  :src="tag !== '极低血糖' ? (tag === '高血糖' ? highIcon : low3Icon) : lowIcon" alt="">
                  {{tag}}
                </span>
              </p>
            </div>
          </div>
        </li>
      </ul>
    </div>
    <Loading :showLoading="showLoading" />
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import lowIcon from '../../../assets/HomeView/low.png'
import low3Icon from '../../../assets/HomeView/low3.png'
import highIcon from '../../../assets/HomeView/high.png'
import Loading from '../../Shared/Loading/Loading'
export default {
  name: 'DepartPatientList',
  components: {
    Loading
  },
  data () {
    return {
      sortPatients: [],
      lowIcon: lowIcon,
      low3Icon: low3Icon,
      highIcon: highIcon,
      showLoading: false,
      showFilter: false,
      filters: [
        {
          rule: 'lowCount',
          text: '近7天低血糖次数从多到少'
        },
        {
          rule: 'testCount',
          text: '近7天测量次数从多到少'
        },
        {
          rule: 'enrollDate',
          text: '入组时间从新到旧'
        },
        {
          rule: 'LastBgDataDate',
          text: '最后一次测血糖时间从新到旧'
        }
      ],
      currentFilter: {
        rule: 'LastBgDataDate',
        text: '最后一次测血糖时间从新到旧'
      }
    }
  },
  computed: {
    ...mapGetters({
      patients: 'doctorPatients',
      sendStatus: 'patientSendStatus',
      user: 'user',
      departWatchDoctor: 'departWatchDoctor'
    })
  },
  methods: {
    sortList (a, b) {
      let result = b[this.currentFilter.rule] - a[this.currentFilter.rule]
      if (result !== 0) {
        return result
      } else {
        return b.enrollDate - a.enrollDate
      }
    },
    changeFilter (filter) {
      this.currentFilter = filter
      this.sortPatients = this.patients.sort(this.sortList)
      this.showFilter = false
    },
    goToDetail (patient) {
      this.$store.dispatch('fetchPatientItem', patient)
      this.$router.push('/patient-detail/' + this.$route.params.id + '/' + patient.PatientId)
    },
    handleShowFilters () {
      this.showFilter = !this.showFilter
    }
  },
  watch: {
    sendStatus: function (val, oldVal) {
      if (val) {
        this.sortPatients = this.patients.sort(this.sortList)
        this.showLoading = false
      }
    }
  },
  beforeMount () {
    this.showLoading = true
    this.$store.dispatch('fetchPatientByDoctor', this.$route.params.id)
  }
}
</script>
<style lang="scss" scoped>
  @import "./style.scss";

</style>



